<template>
  <div class="note" :style ="note">
    <el-menu :default-active="$route.path" class="el-menu-demo" mode="horizontal" router>
      <el-menu-item index="/userSubmit">新建任务</el-menu-item>
      <el-menu-item index="/queryHistoryTaskU">历史任务</el-menu-item>
    </el-menu>
    <router-view/>
    <div class="exit">
        <el-button type="info" plain @click="exit">退出</el-button>
    </div>
  </div>
</template>
<script>
  export default {
    methods: {
      handleSelect(key, keyPath) {
        console.log(key, keyPath);
      },
      exit () {
        this.$axios.get('exit').then((res) => {
            this.$message('用户已下线');
        });
        this.$router.push({
          path: '/login'
        });
        localStorage.setItem('username','');
      }
    },
};
</script>
<style>
.exit {
    left:70%;
    top:10%;

}
</style>



