<template>
  <div>
    <div class="exit">
        <el-button type="info" plain  @click="exit">退出</el-button>
      </div>
    <el-menu :default-active="activeIndex" class="el-menu-demo" mode="horizontal" @select="handleSelect" router>
      <el-menu-item index="queryTaskZ">任务列表</el-menu-item>
      <el-menu-item index="queryHistoryTaskZ">历史列表</el-menu-item>
    </el-menu>
    <router-view/>
  </div>
</template>
<script>
  export default {
    methods: {
      exit () {
        this.$axios.get('exit').then((res) => {
            this.$message('用户已下线');
        });
        this.$router.push({
          path: '/login'
        }),
        localStorage.setItem('username','');
      }
    },
  };
</script>
<style>
.exit {
    left:70%;
    top:10%;
}
</style>



