<template>
  <div>
    <div>
      <el-input
        placeholder="项目Id"
        v-model="taskId"
        :disabled="true">
      </el-input>
    </div>
    <div>
      <el-input
        type="textarea"
        :rows="2"
        placeholder="请输入内容"
        v-model="textarea">
      </el-input>
    </div>
    <div>
      <el-radio v-model="radio" label="同意">同意</el-radio>
      <el-radio v-model="radio" label="不同意">不同意</el-radio>
    </div>
    <div>
      <el-radio v-model="radio1" label="业务员">业务员</el-radio>
      <el-radio v-model="radio1" label="副经理">副经理</el-radio>
    </div>
    <el-button type="primary" @click="click(textarea,radio,taskId,radio1)">提交</el-button>
  </div>
</template>
<script>
  export default {
    data () {
      return {
        textarea: '',
        taskId: '',
        radio: '',
        radio1: '',
        routerParams: ''
      }
    },
    methods: {
      click(textarea, radio,taskId,radio1) {
        this.$axios.post('/approvalByZ', {
          text: textarea,
          msg1: radio,
          taskId: taskId,
          choice: radio1,
        }).then((res) => {
          if (res.data.code === 0) {
            this.$message('成功');
          } else {
            this.$message.error('服务器异常，请刷新再试');
          }
        })
      }
    },
    created () {
      this.taskId = this.$route.query.routerParams;
    },
  }
</script>

<style>

</style>




