<template>
  <div>
    <el-button @click="exit"></el-button>
  </div>
</template>

<script>
  export default {
    methods:{
      exit() {
        this.$axios.get('/exit').then((res) => {
          alert("退出成功")
        })
      }
    }
  }
    
</script>
<style>

</style>



