<template>
  <div>
    <el-table
      border
      :data="tableData"
      style="width: 100%">
      <el-table-column
        prop="startTime"
        label="日期"
        width="180">
      </el-table-column>
      <el-table-column
        prop="name"
        label="状态"
        width="180">
      </el-table-column>
      <el-table-column
        prop="textF"
        label="副经理意见">
      </el-table-column><el-table-column
        prop="textZ"
        label="总经理意见">
      </el-table-column><el-table-column
        prop="id"
        label="项目Id">
      </el-table-column><el-table-column
        prop="display"
        label="结果">
      </el-table-column><el-table-column
        v-show= "zong"
        prop="endtime"
        label="结束时间">
      </el-table-column><el-table-column
      fixed="right"
      label="操作"
      width="100">
      <template slot-scope="scope">
        <el-button  @click="edit(scope.row)" type="text" size="small">编辑</el-button>
      </template>
    </el-table-column>
    </el-table>
  </div>
</template>

<script>
export default {
  data() {
    return {
      tableData: [],
      zong: 0,
    };
  },
  created() {
    if(this.$route.path == '/queryTaskZ') {
      this.$axios.get('/queryTaskZ').then((res) => {
        this.tableData = res.data.data;
      });
    }else if(this.$route.path == '/queryTaskF'){
      this.$axios.get('/queryTaskF').then((res) => {
        this.tableData = res.data.data;
      });
    }else if (this.$route.path == '/queryTask'){
        this.$axios.get('/queryTask').then((res) => {
        this.tableData = res.data.data;
    });
    } else if (this.$route.path == '/queryHistoryTaskZ') {
      console.log('hahahahah');
      this.$axios.get('/queryHistoryTaskZ').then((res) => {
        this.tableData = res.data.data;
    });
    } else if (this.$route.path == '/queryHistoryTaskF') {
      this.$axios.get('/queryHistoryTaskF').then((res) => {
        this.tableData = res.data.data;
    });
    } else if (this.$route.path == '/queryHistoryTaskU'){
      this.$axios.get('/queryHistoryTaskU').then((res) => {
        this.tableData = res.data.data;
    });
    } else if(this.$route.path == '/queryHistoryTaskZ'|| this.$route.path == '/queryHistoryTaskU' || this.$route.path == '/queryHistoryTaskF') {
        this.zong = this.zong;
        this.ha = this.ha;
    } else  {
      this.zong = !this.zong;
    }
  },
  methods: {
    edit (taskId) {
        if (this.$route.path == '/queryTaskF') {
          this.$router.push({
            path: '/approvalByF',
            query: {
              routerParams: taskId.id
            }
          })
        } else {
          this.$router.push({
            path: '/approvalByZ',
            query: {
              routerParams: taskId.id
            }
          })
        }
    }  
  },
 };
 
</script>
<style>

</style>



