<template>
  <!--rules为验证的规则-->
  <div class="login">
    <h2>登录</h2>
    <el-form :model="loginForm"  :rules="rules" ref="loginForm" label-width="0px">
      <el-form-item prop="ID_" class="username">
        <el-input v-model="loginForm.ID_" placeholder="用户名"></el-input>
      </el-form-item>
      <!--prop为需要验证的字段名-->
      <el-form-item prop="PWD_" class="password">
        <el-input v-model="loginForm.PWD_" placeholder="密码"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="login(loginForm)">登录</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
  export default {
    data () {
      return {
        loginForm: {
          ID_: '',
          PWD_: ''
        },
        rules: {
          ID_: [
            { required: true, message: '请输入用户名', trigger: 'blur' }
          ],
          PWD_: [
            { required: true, message: '请输入密码', trigger: 'blur' }
          ]
        }
      }
    },
    methods: {
      login (loginForm) {
        this.$refs.loginForm.validate((valid) => {
          if (valid) {
            this.$axios.post('login', {
              username: this.loginForm.ID_,
              password: this.loginForm.PWD_
            }).then((res) => {
              localStorage.setItem('username', this.loginForm.ID_);
              if (res.data.data === 'manager') {
                this.$router.push('/createMembership')
              } else if (res.data.data === '业务员') {
                this.$router.push('/yewuyuan')
              } else if (res.data.data === '副经理') {
                this.$router.push('/fujingli')
              } else if (res.data.data === '总经理') {
                this.$router.push('/zongjingli')
              } else if (res.data.code === 2) {
                this.$message.error('用户名或密码错误！')
                return false
              }
            })
          } else {
            console.log('error submit!!')
            return false
          }
        })
      }
    }
  };
</script>

<style>
  .login {
    position: absolute;
    left:50%;
    top:50%;
    width:300px;
    height:160px;
    margin:-150px 0 0 -190px;
    padding:40px;
    border-radius: 5px;
  }
</style>



