<template>
  <div>
    <div>
      <el-input
        placeholder="项目Id"
        v-model="taskId"
        :disabled="true">
      </el-input>
    </div>
    <div>
      <el-input
        type="textarea"
        :rows="2"
        placeholder="请输入内容"
        v-model="textarea">
      </el-input>
    </div>
    <div>
      <el-radio v-model="radio" label="同意">同意</el-radio>
      <el-radio v-model="radio" label="不同意">不同意</el-radio>
    </div>
     <el-button type="primary" @click="click(textarea,radio,taskId)">提交</el-button>
   </div>
</template>
<script>
import bus from '../common/Bus.js';
export default {
  data() {
    return {
      input: '',
      radio: '同意',
      taskId: '',
    };
  },
  methods: {
    click(textarea,radio,taskId) {
      this.$axios.post('approvalByF', {
        text: textarea,
        msg1: radio,
        taskId: taskId,
      }).then((res) => {
        console.log(res);
        if (res.data.code === 0) {
            this.$message('成功');
        } else {
            this.$message.error('服务器错误，请刷新再试');
        }
      });
    },
  },
  created () {
    console.log(this.$route.query.routerParams);
    this.taskId = this.$route.query.routerParams;
    },
};
</script>

<style>

</style>




