import Vue from 'vue';
import Router from 'vue-router';
import Login from '../components/page/login';
import Root from '../components/page/Root';
import Home from '../components/page/Home';
import UpLoad from '../components/common/UpLoad';
import List from '../components/common/List';
import FuJingLi from '../components/page/FuJIngLi';
import FuPi from '../components/page/FuPi';
import ZongJingLi from '../components/page/ZongJingLi';
import ZoPi from '../components/page/ZoPi';
import Tset from '../components/common/tset';
import Test2  from '../components/common/Test2';
import Edit from '../components/common/Edit';

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/login',
    },
    {
      path: '/login',
      meta: { title: '登录' },
      component: Login,
    },
    {
      path: '/createMembership',
      meta: { title: '管理员' },
      component: Root,
    },
    {
      path: '/yewuyuan',
      meta: { title: '业务员' },
      component: Home,
      children: [
        {
          path: '/userSubmit',
          meta: { title: '上传文件' },
          component: UpLoad,
        },
        {
          path: '/queryTask',
          meta: { title: '个人任务列表' },
          component: List,
        },
        {
          path: '/queryHistoryTaskU',
          meta: { title : '任务历史'},
          component: List,
        }
      ],
    },
    {
      path: '/fujingli',
      meta: { title: '副经理' },
      component: FuJingLi,
      children: [
        {
          path: '/approvalByF',
          meta: { title: '副经理审批' },
          component: FuPi,
        },
        {
          path: '/queryTaskF',
          meta: { title: '个人任务列表' },
          component: List,
        },{
          path: '/queryHistoryTaskF',
          meta: { title : '任务历史'},
          component: List,
        },
      ],
    },
    {
      path: '/zongjingli',
      meta: { title: '总经理' },
      component: ZongJingLi,
      children: [
        {
          path: '/approvalByZ',
          meta: { title: '总经理审批' },
          component: ZoPi,
        },
        {
          path: '/queryTaskZ',
          meta: { title: '个人任务列表' },
          component: List,
        },
        {
          path: '/queryHistoryTaskZ',
          meta: { title : '任务历史'},
          component: List,
        },
      ],
    },
    {
      path: '/cccc',
      component: Tset
    },
    {
      path: '/mmmm',
      component: Edit
    }
  ],
});



