<template>
  <div>
    <h2>身份设置</h2>
    <el-form :inline="true" :model="formInline" ref="formInline" class="demo-form-inline">
      <el-form-item label="身份">
        <el-select v-model="formInline.user" placeholder="身份">
          <el-option label="业务员" value="业务员"></el-option>
          <el-option label="副经理" value="副经理"></el-option>
          <el-option label="总经理" value="总经理"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="用户账号">
        <el-input v-model="formInline.userId" placeholder="用户账号"></el-input>
      </el-form-item>
      <el-form-item label="密码">
        <el-input v-model="formInline.password" placeholder="密码"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="onSubmit(formInline)">新建</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
  export default {
    data () {
      return {
        formInline: {
          user: '',
          password: '',
          userId: ''
        }
      }
    },
    methods: {
      onSubmit (formInline) {
        this.$refs.formInline.validate((valid) => {
          if (valid) {
            this.$axios.post('createMembership', {
              password: this.formInline.password,
              userId: this.formInline.userId,
              groupId: this.formInline.user
            }).then((res) => {
              localStorage.setItem('username', this.loginForm.ID_)
              if (res.data.code == 1) {
                alert("添加成功");
                this.$message({
                  message: '创建成功',
                  center: true
                })
              }
            })
          } else {
            this.$message({
              message: '创建失败',
              center: true
            })
            return false
          }
        })
      }
    }
  }
</script>
<style>

</style>



