<template>
  <div>
    <h2>文件上传</h2>
     <el-upload
      class="upload-demo"
      drag
      :on-success="success"
      :on-error="error"
      :data="input"
      action="http://localhost:8080/userSubmit"
      multiple>
      <i class="el-icon-upload"></i>
      <div class="el-upload__text">点击上传<em></em></div>
      <div class="el-upload__tip" slot="tip">文件大小不超过500kb</div>
    </el-upload> 
  </div>
</template>
<script>
  export default {
    success (response, file, fileList) {
        this.$message('上传成功');
    },
    error (response, file, fileList) {
        this.$message('服务器异常，请刷新重试');
    },
  };
</script>
<style>
.message {
  position: absolute;
    left:50%;
    top:80%;
    width:300px;
    height:160px;
    margin:-150px 0 0 -190px;
    padding:40px;
    border-radius: 5px;
}
</style>



